import { RefObject, useEffect } from 'react';

/**
 * @example useItemIntersection(listRef,offset,() => { do_some_thing; });
 * @param listRef The ref of the List.
 * @param offset  Index of the list item which needs to be observed.
 * @param cb Callback that needs to be fired on intersect
 * @returns void.
 */

const useItemIntersection = (
  listRef: RefObject<HTMLUListElement | HTMLOListElement>,
  offset: number,
  cb: () => void
) => {
  useEffect(() => {
    if (listRef.current) {
      let listItem: HTMLLIElement | null = null;
      if (offset > 0) {
        const listItems: NodeListOf<HTMLLIElement> =
          listRef.current.querySelectorAll(':scope > li');
        if (listItems.length > offset) {
          listItem = listItems[offset];
        }
      }
      if (listItem) {
        const intersectionObserver = new IntersectionObserver(
          (entries: IntersectionObserverEntry[]) => {
            if (entries[0].isIntersecting) {
              cb();
            }
          },
          {
            root: listRef.current
          }
        );
        intersectionObserver.observe(listItem);
        return () => {
          intersectionObserver.disconnect();
        };
      }
    }
  }, [cb, listRef.current, offset]);
};

export default useItemIntersection;
