import styled, { css } from 'styled-components';

import {
  calculateFontSize,
  defaultThemeProp,
  FontSize,
  StyledLink,
  StyledPopover
} from '@pega/cosmos-react-core';

import { RichTextEditorProps } from './RichTextEditor.types';
import { getTableStyles } from './DecoratorComponents/Table';

interface StyledRichTextEditorProps {
  toolbar?: boolean;
  focused?: boolean;
  readOnly?: boolean;
  containerHeight?: RichTextEditorProps['height'];
}

export const listStyles = css`
  ul,
  ol {
    padding-inline-start: 2.5rem;
  }

  li {
    margin: 0.5rem 0;
  }

  ul li {
    ul {
      margin-inline-start: 1rem;
      list-style-type: circle;
    }

    ul ul {
      margin-inline-start: 2rem;
      list-style-type: square;
    }

    ul ul ul {
      margin-inline-start: 3rem;
      list-style-type: disc;
    }
  }

  ol li {
    ol {
      margin-inline-start: 1rem;
      list-style-type: lower-alpha;
    }

    ol ol {
      margin-inline-start: 2rem;
      list-style-type: lower-roman;
    }

    ol ol ol {
      margin-inline-start: 3rem;
      list-style-type: decimal;
    }
  }

  ${StyledPopover} ul, ${StyledPopover} ol {
    padding-inline-start: 0;
  }

  ${StyledPopover} li {
    margin: 0;
  }
`;

export const EditorContainer = styled.div<StyledRichTextEditorProps>(
  ({ containerHeight, toolbar, theme }) => {
    return css`
      padding: ${toolbar ? theme.base.spacing : '0'};
      height: ${typeof containerHeight === 'string' ? containerHeight : 'auto'};
      min-height: ${typeof containerHeight === 'string' ? 'auto' : containerHeight?.min || 'auto'};
      max-height: ${typeof containerHeight === 'string' ? 'auto' : containerHeight?.max || 'auto'};
      overflow: auto;

      &:hover {
        cursor: text;
      }

      ${listStyles}
    `;
  }
);

EditorContainer.defaultProps = defaultThemeProp;

const StyledRichTextEditor = styled.div<StyledRichTextEditorProps>(({ toolbar, theme }) => {
  return css`
    background-color: ${toolbar ? theme.base.palette['primary-background'] : 'transparent'};
    width: 100%;
    position: relative;

    &:focus-within:not([disabled]) {
      border-color: ${theme.components['form-control'][':focus']['border-color']};
      box-shadow: ${theme.components['form-control'][':focus']['box-shadow']};
    }

    /* Override default styling of slate placeholder. */
    span[contenteditable='false'] {
      opacity: ${theme.base.transparency['transparent-3']} !important;
    }
  `;
});

StyledRichTextEditor.defaultProps = defaultThemeProp;

export const StyledFormField = styled.div<StyledRichTextEditorProps>(({ toolbar }) => {
  return css`
    ${StyledRichTextEditor} {
      ${!toolbar &&
      css`
        border: none;
        box-shadow: none;
      `}
    }
  `;
});

export const StyledRichTextViewer = styled.div`
  width: 100%;
  position: relative;

  ${StyledLink} {
    &:enabled:focus,
    &:not([disabled]):focus {
      box-shadow: none;
      text-decoration: underline;
    }
  }
`;

export const StyledHtml = styled.div(({ theme }) => {
  const {
    base: { 'font-size': baseFontSize, 'font-scale': baseFontScale, palette },
    components: { text }
  } = theme;
  const fontSize = calculateFontSize(baseFontSize, baseFontScale);
  return css`
    ${listStyles}
    table {
      ${getTableStyles(theme)}
    }
    img {
      max-width: 100%;
    }
    a {
      color: ${palette.interactive};
    }
    pre {
      white-space: break-spaces;
    }
    p {
      min-height: ${fontSize[text.primary['font-size'] as FontSize]};
    }
    h1 {
      font-size: ${fontSize[text.h1['font-size'] as FontSize]};
      font-weight: ${text.h1['font-weight']};
      min-height: ${fontSize[text.h1['font-size'] as FontSize]};
    }
    h2 {
      font-size: ${fontSize[text.h2['font-size'] as FontSize]};
      font-weight: ${text.h2['font-weight']};
      min-height: ${fontSize[text.h2['font-size'] as FontSize]};
    }
    h3 {
      font-size: ${fontSize[text.h3['font-size'] as FontSize]};
      font-weight: ${text.h3['font-weight']};
      min-height: ${fontSize[text.h3['font-size'] as FontSize]};
    }
    h4 {
      font-size: ${fontSize[text.h4['font-size'] as FontSize]};
      font-weight: ${text.h4['font-weight']};
      min-height: ${fontSize[text.h4['font-size'] as FontSize]};
    }
    h5 {
      font-size: ${fontSize[text.h5['font-size'] as FontSize]};
      font-weight: ${text.h5['font-weight']};
      min-height: ${fontSize[text.h5['font-size'] as FontSize]};
    }
    h6 {
      font-size: ${fontSize[text.h6['font-size'] as FontSize]};
      font-weight: ${text.h6['font-weight']};
      min-height: ${fontSize[text.h6['font-size'] as FontSize]};
    }
  `;
});

StyledHtml.defaultProps = defaultThemeProp;

export default StyledRichTextEditor;
